*{
    margin: 0;
    padding: 0;
    
}
.navbar{
    width: 100vw;
    /* min-width: 150vh; */
    display: flex;
    justify-content: space-between;
    /* border: 2px solid red; */
    align-items: center;
    overflow: hidden;
}
.navlist {
    display: flex;
    gap: 20px;
}
.logo{
    margin: 5px 10px;
    display: flex;
    align-items: center;
    gap:10px;
    font-size: 1.5rem;
    font-weight: bolder;
    transition: all 1s;
}
.logo:hover{
    transform: scale(1.1);
}
.logo div p{
    font-size: 1rem;
    font-weight: bolder;
}
.background{
    background: url(https://media-exp1.licdn.com/dms/image/C4E03AQGPfF40OVpmEA/profile-displayphoto-shrink_100_100/0/1641878275176?e=1671062400&v=beta&t=sbQH0XnB29EbB1IOUouRc1EppvSELXGVyfMP21L53U8);
    background-size: cover;
    height: 3rem;
    width: 3rem;
    border-radius: 50px;
}
.navbar p{
    font-family:sans-serif  ;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.7);
    font-size: 1rem;
    transition: all 1s;
}
.navbar .navlist p:hover{
    font-weight: bold;
    color: black;
    cursor: pointer;
}
input{
    height: 1.5rem;
    border: transparent;
    width: 100px;
}
input:focus{
    outline: none;
}
.search{
    border: 1px solid rgb(63, 62, 62);
    border-radius: 10px;
    padding: 8px;
    margin-right: 50px;
    display: flex;
    gap: 5px;
    align-items: center;
    overflow: hidden;
}
@media only screen and (max-width:600px){
    .logo{
        position: absolute;
        top: 5px;
        left: 5px;
    }
    .search{
        position: absolute;
        top: 15px;
        right: 5px;
    }
    .navlist{
        margin-top: 80px;
        justify-content: center;
        width: 100vw;
    }
}