.rp {
    min-height: 100vh;
    display: flex;
    overflow: hidden;
}

.rpheading {
    /* border: 2px solid green; */
    display: flex;
    flex-direction: column;
}

.rpheading p {
    margin-left: 20px;
    font-size: 5rem;
    font-weight: bolder;
    font-family: sans-serif;
}

.rpheading span {
    margin-bottom: -20px;
    color: rgb(157, 157, 250);
    transform: rotateZ(-30deg);
    width: 20px;
    font-weight: 700;
}

.rpleft {
    display: flex;
    flex-direction: column;
    margin: 0px 30px;
    margin-top: 100px;
    margin-bottom: 50px;
    justify-content: center;
    /* padding: 0; */
    align-items: center;
}

.project {
    border-top: 3px solid black;
    height: unset;
    margin-top: 10vh;
    /* margin-bottom: 5vh; */
    transition: all .5s;
    overflow: hidden;
}

.project:hover {
    box-shadow: 1px 1px 10px 6px rgba(0, 0, 0, 0.3);
    border-radius: 10px;

}

.project img {
    height: 18vw;
    width: 30vw;
}

.project p {
    padding: 5px;
}

.project div {
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.project div a img {
    height: 30px;
    width: 30px;
    border-radius: 50%;
}

.project div p {
    font-size: 1.5rem;
}

.rpright {
    justify-content: center;
    /* align-items: center; */
    display: flex;
    gap: 30px;
}

#project2 {
    margin-top: 35vh;
    height:23vw;
}

#project3 {
    margin-top: 15vh;
}

#rpviewmore img {
    height: 50px;
    width: 50px;
    border-radius: 50%;
}

#rpviewmore {
    margin-top: 25vh;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

@media only screen and (max-width:1100px) {
    .rp {
        flex-direction: column;
        margin: 0;
    }

    .rpright {
        flex-direction: column;
        margin: 0;
        justify-content: center;
        align-items: center;
        width: 100vw;
        gap: 100px;
    }

    #project3 {
        margin: 0;
    }

    #project2 {
        margin: 0;
        height: unset;
    }

    .project {
        max-width: 60vw;
        display: flex;
        flex-direction: column;
    }

    .project img {
        height: 36vw;
        width: 60vw;
    }

    #rpviewmore {
        margin-top: 50px;
    }

    .project div p {
        font-size: 3vw;
    }

    .project div {
        padding: 5px 0;
    }
}