.homepage{
    /* border: 3px solid black; */
    display: flex;
    gap: 5px;
    min-height: 100vh;
    /* max-width: 100vw; */
    justify-content: space-between;
    width: 100vw;
    /* border: 2px solid red; */
    /* padding: 20vh; */
    overflow: hidden;
    /* height: 100vh; */
}
a{
    color: unset;
}
.left{
    /* border: 2px solid red; */
    padding-top: 70px;
    margin-left: 10vw;
    max-height: 500px;   
    /* max-width: 500px; */
}
.heading{
    /* border: 2px solid green; */
    display: flex;
    flex-direction: column;
}
.heading p{
    margin-left: 20px;
    font-size: 5rem;
    font-weight: bolder;
    font-family: sans-serif;
}
.heading span{
    margin-bottom: -20px;
    color: rgb(157, 157, 250);
    transform: rotateZ(-30deg);
    width: 20px ;
    font-weight: 700;
}
.desc{
    margin-top: 20px;
    display: flex;
    gap: 30px;
    /* border: 2px solid blue; */
}
.desc span{
    font-weight: bolder;
}
.desc p{
    letter-spacing: 1px;
}
.buttons{
    margin-top: 50px;
    display: flex;
    gap: 20px;
}
.first:hover{
    
    transform: scale(1.1);
}
.second:hover{
  
    transform: scale(1.1);
}
button{
    border: 0px;
    background-color: transparent;
    margin-right: 5px;
}
.buttons .first{
    transition: all .5s;
    padding: 20px;
    background-color: rgb(157, 157, 250);
    border-radius: 15px;
}
.buttons .second{
    transition: all .5s;
    padding: 20px;
    background-color: rgb(157, 157, 250);
    border-radius: 15px;
}
.networks{
    margin-top: 70px;
   display: flex;
   /* justify-content: center; */
   align-items: flex-end;
   gap: 50px;
   position:relative ;
   bottom: 20px;
  
}
.networks a{
    transition: all .3s;
}
.networks a:hover{
    transform: scale(1.2);
}
.networks i{
    font-size: 1.5rem;
}
.right{
    display: flex;
    padding: 2rem;
    padding-right: 10vw;
    justify-content: center;
    align-items: center;
}   

.hexagon{
    margin-top: -50px;
    overflow: hidden;
    transition: all 1.5s;
}
.hexagon:hover{
    transform: scale(1.15);  
}

/* #ffdf00 */
.right .tagline{
    position: absolute;
    right: 15px; 
    top: 80px;
    font-size: 1.2rem;
    font-weight: bold;
}
.profile{
    /* background-image: url(../image/profile.png); */
    background-repeat: no-repeat;
    background-position: cover;
    background-color: rgb(251, 212, 16);
    z-index: 4 ;
}
.hexagon img{
    height: 43vw;
    width: 43vw;
    z-index: inherit;
}
@media only screen and (max-width:1000px){
    .homepage{
        flex-direction: column-reverse;
        justify-content: center;
    }
    .heading p{
        font-size: 10vw;
        display: inline-block;
        width: 50vw;
        /* text-align: center; */
    }
    .heading span{
        transform: rotateZ(0deg);
        margin-bottom: 10px;
    }
    .desc{
        gap: 2vw;
        display: flex;
        justify-content: center;
    }
    .heading{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .left{
        padding: 0;
        margin-top: 0px;
        /* margin:0 15vw ; */
        margin: 0;
        justify-content: center;
    }
    .right{
        margin-top: 50px;
        padding: 0;
    }
    .networks{
        position: relative;
        display: flex;
        justify-content: center;
    }
    .buttons{
        display: flex;
        justify-content: center;
    }
    .hexagon img{
        height: 80vw;
        width: 80vw;
    }
}
@media only screen and (max-width:600px){
    .right .tagline{
        top: 120px;
    }
    .right{
        margin-top: 90px;
    }
    .networks{
        gap: 20px;
    }
}