.footer{
    min-height: 100vh;
    background-color: rgb(251, 212, 16);
}
footer{
    display: flex;
    justify-content: space-between;
    border-top: 3px solid black;
    padding: 15px 10px;
}
.footer>div{
    display: flex;
}
.fleft{
    width: 55vw;
    /* border: 2px solid red; */
    margin:0 7vw;
    margin-right: 20px;
    margin-bottom: 5vw;
}
.fheading{
    /* border: 2px solid green; */
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    margin-bottom: 100px;
}
.fheading p{
    margin-left: 20px;
    font-size: 5rem;
    font-weight: bolder;
    font-family: sans-serif;
}
.fheading span{
    margin-bottom: -20px;
    color: rgb(157, 157, 250);
    transform: rotateZ(-30deg);
    width: 20px ;
    font-weight: 700;
}
.fel{
    border-top: 2px solid black;
    padding: 20px 0;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    gap: 30px;
}
.fel div p{
    font-size: 1.5rem;
    font-weight: bold;
}
.fimage img{
    height: 30px;
    width: 30px;
}
.fimage{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35px;
    height: 35px;
    background-color: white;
    padding: 10px;
    border-radius: 50%;
}








/* right components css */
.fright{
    padding-top: 3vw;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
}
.fright .circle{
    height: 40vw;
    width: 40vw;
    margin-bottom: 5vw;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid black;
    border-radius: 50%;
    
}
.circle div a img{
    height: 150px;
    width: 150px;
    border-radius: 50%;
}
.circle div{
    display: flex;
    justify-content: space-between;
    width: 30vw;
    align-items: flex-end;
    border-bottom: 2px solid black;
    transform: rotate(20deg);
    padding-bottom: 20px;
}
.circle div p{
    transform: rotate(-20deg);
    font-size: 1.5rem;
}
.circle div p span{
    font-size: 3rem;
}
.circle div a{
    transform: rotate(-20deg);
}

    
@media only screen and (max-width:800px){
    .footer>div{
        flex-direction: column-reverse;
    }
    .fleft{
        width: 80vw;
        
    }
    .fright{
        margin-top: 50px;
    }
    .fright .circle{
        height: 80vw;
        width: 80vw;
    }
    .circle div{
        width: 70vw;
    }
    .footer{
        overflow: hidden;
    }
    .fheading{
        margin-top: 50px;
        margin-bottom: 50px;
    }
    .fheading p{
        font-size: 2.5rem;
    }
    .circle div a img{
        height: 20vw;
        width: 20vw;
        border-radius: 50%;
    }
    footer{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 5px;
    }
    .circle{
        margin-bottom: 0;
    }
}