.services{
    min-height: 100vh;
    overflow: hidden;
    background-color: rgb(251, 212, 16);
}
.uperPart{
    display: flex;
    justify-content: center;
    align-items: center;
   /* border: 3px solid red;  */
   /* border-bottom: 2px solid white; */
}
.left{
    /* border-right: 2px solid white; */
    /* padding-right: 10vw; */
}
.desc{
    width: 40vw;
}
#skills{
    position: absolute;
    /* left: 5px; */
    margin: -20px;
    color: rgb(157, 157, 250);
    transform: rotateZ(-30deg);
    width: 50px ;
    font-weight: 700;
}
.services{
    display: flex;
    flex-direction: column;

}
.left >p{
    font-size: 5rem;
}
.right{
    display: flex;
    flex-direction: column;
    justify-items: flex-start;
    align-items: center;
    margin: 0;
    padding-top: 100px;
}
.right>span{
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 20px;
    /* position: absolute; */
    left: 2px;
    margin-left: -20vw;
}
.experinces{
    width: 40vw;    
    display: flex;
    gap: 40px;
    border-top: 2px solid black;
    align-items: center;
    padding: 20px;
}
.experinces p{
    font-weight: bold;
}
.ex{
    font-size: 1.5rem;
}
.lowerPart{
    display: flex;
    /* border: 2px solid red; */
    flex-wrap: wrap;
    padding: 20px;
    gap: 20px;
    justify-content: space-between;
    /* height: 50vh; */
}
.skills{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}
.skills img:hover{
    transform: rotate(360deg);
}
.skills img{
    padding: 20px;
    border-radius: 50%;  
    background-color: black;
    height: 80px;
    width: 80px;
    transition: all 2s;
}
@media only screen and (max-width:1000px){
    .uperPart{
        margin-top: 100px;
        flex-direction: column;
    }
    .desc{
        width: 80vw;
    }
    .left>p{
        font-size: 8vw;
        text-align: center;
    }
    .left{
        justify-content: center;
    }
    .experinces{
        width: 80vw;
        gap: 20vw;
    }
}
@media only screen and (max-width:500px){
    .left>p{
        font-size: 15vw;
    }
    .lowerPart{
        padding: 0 10vw;
        gap:15px;
    }
}